
import XHeader from 'vux/src/components/x-header/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import {mapState} from 'vuex'

const baseList = [{
url: 'javascript:',
img: './static/images/swiper_1.jpg'
}, {
url: 'javascript:',
img: './static/images/swiper_2.jpg'
}, {
url: 'javascript:',
img: './static/images/swiper_3.jpg'
}]
const urlList = baseList.map((item, index) => ({
url: 'http://cn.bing.com',
img: item.img,
fallbackImg: item.fallbackImg
}))

export default {
components: {
Swiper,
XButton,
XHeader,
Search,
Cell,
Group,
Flexbox,
FlexboxItem
},
ready () {
},
mounted () {
this.$http({
method: 'GET',
url: './static/data/songmenu.json'
})
.then(response => {
this.recommendSongList = response.data
})




},
computed: {
...mapState([
'test', 'activeName'
])










},
methods: {
changeIndex (index) {
this.index = index
},
showIndex (index) {
this.$vux.toast.show({
type: 'text',
position: 'top',
text: index
})
},
unOpen () {
this.$vux.confirm.show({
title: '尴尬提示',
content: '尚未开放!'
})
},
change () {
this.$store.dispatch('changeTest', 'op')
},
resultClick (item) {
window.alert('you click the result item: ' + JSON.stringify(item))
},
getResult (val) {
console.log('on-change', val)
this.results = val ? getResult(this.value) : []
},
onSubmit () {
this.$refs.search.setBlur()
this.$vux.toast.show({
type: 'text',
position: 'top',
text: this.value ? this.value : this.placeholderValue
})
},
onFocus () {
this.isfocus = !this.isfocus
console.log('on focus')
},
onCancel () {
this.isfocus = !this.isfocus
console.log('on cancel')
}
},
data () {
return {
list: urlList,
index: 0,
results: [],
value: '',
placeholderValue: '红昭愿',
isfocus: false,
url: './static/images/cover.jpg',
recommendSongList: []
}
}
}
function getResult (val) {
let rs = []
for (let i = 0; i < 20; i++) {
rs.push({
title: `${val} result: ${i + 1} `,
other: i
})
}
return rs
}



